}
#xe-woo-vertical-navigation[xe-woo-navigation-open-behaviour=hover]:hover [xe-woo-navigation-dropdown-animation=FadeInDown],
#xe-woo-vertical-navigation[xe-woo-navigation-open-behaviour=click].visible [xe-woo-navigation-dropdown-animation=FadeInDown] {
