.woocommerce-checkout #payment ul.payment_methods.wc_payment_methods {
  padding: 20px;
  border: 2px dashed #E6E6E6;
  border-radius: 4px;
}

.woocommerce-cart #payment ul.payment_methods li,
#add_payment_method #payment ul.payment_methods li,
.woocommerce-checkout #payment ul.payment_methods li {
  font-size: var(--xe_base_font_size);
  font-weight: var(--xe_base_font_weight);
  line-height: var(--xe_base_font_line_height);
  background-color: #EFEFEF;
}

.woocommerce-cart #payment ul.payment_methods li.wc_payment_method,
#add_payment_method #payment ul.payment_methods li.wc_payment_method,
.woocommerce-checkout #payment ul.payment_methods li.wc_payment_method {
  display: block;
  margin-bottom: 15px;
  padding-bottom: 15px;
  border-bottom: 1px solid #E6E6E6;
  background-color: transparent;
}

.woocommerce-cart #payment ul.payment_methods li.wc_payment_method:last-child,
#add_payment_method #payment ul.payment_methods li.wc_payment_method:last-child,
.woocommerce-checkout #payment ul.payment_methods li.wc_payment_method:last-child {
  margin-bottom: 0;
  padding-bottom: 0;
  border-bottom: none;
}

.woocommerce-cart #payment ul.payment_methods li.wc_payment_method > p,
#add_payment_method #payment ul.payment_methods li.wc_payment_method > p,
.woocommerce-checkout #payment ul.payment_methods li.wc_payment_method > p {
  font-size: var(--xe_base_font_size);
  font-size: calc(var(--xe_base_font_size) - 1px);
}

.woocommerce-cart #payment div.payment_box,
#add_payment_method #payment div.payment_box,
.woocommerce-checkout #payment div.payment_box {
  padding: 0;
  margin: 10px 0;
  background-color: transparent;
}

.woocommerce-cart #payment div.payment_box::before,
#add_payment_method #payment div.payment_box::before,
.woocommerce-checkout #payment div.payment_box::before {
  content: "";
  display: none;
}

.woocommerce-cart #payment div.form-row,
#add_payment_method #payment div.form-row,
.woocommerce-checkout #payment div.form-row {
  padding: 20px 0 0 0;
  margin: 30px 0 0 0;
  border-top: 2px dashed #E6E6E6;
}

.woocommerce .woocommerce-checkout-review-order .woocommerce-privacy-policy-text p,
.woocommerce-page .woocommerce-checkout-review-order .woocommerce-privacy-policy-text p {
  font-size: var(--xe_base_font_size);
  font-size: calc(var(--xe_base_font_size) - 1px);
  font-weight: var(--xe_base_font_weight);
  line-height: calc(var(--xe_base_font_line_height) - 0.1em);
  margin-bottom: 20px;
}

.woocommerce #payment #place_order,
.woocommerce-page #payment #place_order {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  position: relative;
  justify-content: center;
  align-items: center;
  float: none;
  width: 100%;
}

/* -------------------------------------------------------------------------- 
	#. Media Query
   -------------------------------------------------------------------------- */
@media (max-width: 575px) {
  .woocommerce form.woocommerce-checkout {
    margin-top: 40px;
    margin-bottom: 40px;
  }

  .woocommerce-page form.checkout .col2-set .col-1,
.woocommerce-page form.checkout .col2-set .col-2 {
    width: 100%;
  }

  .woocommerce-page form.checkout .col2-set .col-2 {
    position: relative;
    margin-top: 40px;
  }
}
.woocommerce-order-received .woocommerce-thankyou-order-received {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  padding: 15px;
  max-width: 800px;
  border-radius: 3px;
  margin-bottom: 30px;
  margin-left: auto;
  margin-right: auto;
  border: 2px dashed var(--xe_border_color);
}

.woocommerce-order-received .woocommerce-thankyou-order-received:before {
  content: "\e6a0";
  display: inline-flex;
  align-items: center;
  flex-wrap: wrap;
  font-family: var(--xe_iconfont);
  margin-right: 5px;
}

.woocommerce-order-received .woocommerce ul.order_details {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-start;
  padding: 0;
  margin-bottom: 30px;
  list-style: none;
  border-radius: 5px;
  border: 1px solid var(--xe_border_color);
}

.woocommerce-order-received .woocommerce ul.order_details li {
  flex: 1;
  float: none;
  margin: 0;
  padding: 20px;
  font-size: var(--xe_base_font_size);
  line-height: var(--xe_base_line_height);
  text-transform: inherit;
  border-right: 1px dashed var(--xe_border_color);
}

.woocommerce-order-received .woocommerce ul.order_details li > strong {
  font-size: 14px;
  font-size: calc(var(--xe_base_font_size) - 1px);
  font-weight: var(--xe_base_font_weight);
}

.woocommerce-order-received .woocommerce ul.order_details li:last-child {
  border-right: none;
}

.woocommerce-order-received .woocommerce-order-details h2,
.woocommerce-order-received .woocommerce-customer-details h2,
.woocommerce-order-received .woocommerce-column--shipping-address h2 {
  font-size: 20px;
  font-weight: normal;
  margin: 30px 0 20px 0;
}

.woocommerce-order-received table.shop_table td > a {
  color: var(--xe_text_color);
}

.woocommerce-order-received table.shop_table td > a:hover {
  color: var(--xe_seconary_color);
}

.woocommerce-order-received .woocommerce-customer-details h2,
.woocommerce-order-received .woocommerce-column--shipping-address h2 {
  margin-top: 0;
}

.woocommerce .woocommerce-customer-details address {
  padding: 20px;
  border: 1px solid var(--xe_border_color);
}

/* -------------------------------------------------------------------------- 
	#. Login/Register
   -------------------------------------------------------------------------- */
.woocommerce-account #customer_login h2,
.woocommerce-MyAccount-content h3,
.woocommerce-MyAccount-content .woocommerce-Address-title > h3,
.woocommerce-MyAccount-content .woocommerce-order-details > h2,
.woocommerce-MyAccount-content .woocommerce-order-downloads > h2,
.woocommerce-MyAccount-content .woocommerce-column--billing-address .woocommerce-column__title,
.woocommerce-MyAccount-content .woocommerce-column--shipping-address .woocommerce-column__title {
  font-size: 20px;
  font-weight: normal;
  margin: 0 0 30px 5px;
}

.woocommerce-account .u-columns {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  margin-left: -20px;
  margin-right: -20px;
}

.woocommerce-account .u-columns > [class*=col-] {
  position: relative;
  float: none;
  padding-left: 20px;
  padding-right: 20px;
}

.woocommerce-account .u-columns.col1-set > [class*=col-] {
  width: 100%;
}

.woocommerce-account .u-columns.col2-set > [class*=col-] {
  width: 50%;
}

.woocommerce-account form.login,
.woocommerce-account form.register {
  padding: 30px;
  margin: 0 0 30px 0;
  max-width: 100%;
  border-radius: 5px;
  border: 1px solid var(--xe_border_color);
}

.woocommerce-account form.login p.lost_password {
  margin-top: 20px;
  margin-bottom: 0;
}

/* -------------------------------------------------------------------------- 
	#. Dashboard
   -------------------------------------------------------------------------- */
.woocommerce-MyAccount-navigation ul {
  padding: 30px;
  margin: 0;
  list-style: none;
  border: 1px solid var(--xe_border_color);
  border-radius: 5px;
}

.woocommerce-MyAccount-navigation ul li {
  display: block;
  margin-bottom: 15px;
  padding-bottom: 15px;
  border-bottom: 1px solid var(--xe_border_color);
}

.woocommerce-MyAccount-navigation ul li:last-child {
  margin-bottom: 0;
  padding-bottom: 0;
  border-bottom: none;
}

.woocommerce-account .xe-entry-content .woocommerce-MyAccount-navigation ul li > a {
  color: var(--xe_base_text_color);
}

.woocommerce-account .xe-entry-content .woocommerce-MyAccount-navigation ul li > a:hover {
  color: var(--xe_seconary_color);
}

.woocommerce-MyAccount-navigation ul li > a::before {
  content: "\e665";
  display: inline-block;
  margin-right: 5px;
  font-weight: normal;
  font-family: var(--xe_iconfont);
}

.woocommerce-MyAccount-navigation ul li.woocommerce-MyAccount-navigation-link--dashboard > a::before {
  content: "\e6a9";
}

.woocommerce-MyAccount-navigation ul li.woocommerce-MyAccount-navigation-link--orders > a::before {
  content: "\e687";
}

.woocommerce-MyAccount-navigation ul li.woocommerce-MyAccount-navigation-link--downloads > a::before {
  content: "\e632";
}

.woocommerce-MyAccount-navigation ul li.woocommerce-MyAccount-navigation-link--edit-address > a::before {
  content: "\e61d";
}

.woocommerce-MyAccount-navigation ul li.woocommerce-MyAccount-navigation-link--edit-account > a::before {
  content: "\e602";
}

.woocommerce-MyAccount-navigation ul li.woocommerce-MyAccount-navigation-link--customer-logout > a::before {
  content: "\e682";
}

.woocommerce-MyAccount-content .woocommerce-Address-title > h3 {
  margin: 0 0 10px 0;
}

.woocommerce-MyAccount-content form.edit-account fieldset {
  margin-bottom: 20px;
}

.woocommerce-MyAccount-content .shop_table .woocommerce-MyAccount-downloads-file {
  gap: 5px;
}

.woocommerce-MyAccount-content .shop_table .woocommerce-MyAccount-downloads-file::before {
  content: "\e632";
  display: inline-block;
  font-family: var(--xe_iconfont);
}

/* -------------------------------------------------------------------------- 
	#. WooCommerce Block: Product Listing
   -------------------------------------------------------------------------- */
.wc-block-grid__products {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 0;
  margin: 0 -15px;
}

.wc-block-grid__product.wc-block-grid__product {
  position: relative;
  border: none;
  margin-bottom: 30px;
  padding-left: 15px;
  padding-right: 15px;
}

.wc-block-grid__product.wc-block-grid__product .wc-block-grid__product-link {
  display: block;
  font-size: var(--xe_base_font_size);
  color: var(--xe_base_text_color);
  text-decoration: none;
}

.wc-block-grid__product.wc-block-grid__product .wc-block-grid__product-link:hover {
  color: var(--xe_seconary_color);
  text-decoration: none;
}

.wc-block-grid__product.wc-block-grid__product .wc-block-grid__product-link:focus {
  outline: none;
}

.wc-block-grid__product .wc-block-grid__product-add-to-cart {
  margin-top: 10px;
  margin-bottom: 0;
}

.wc-block-grid__product-add-to-cart.wp-block-button .wp-block-button__link {
  position: relative;
}

.wc-block-grid__product-add-to-cart.wp-block-button .wp-block-button__link:hover {
  text-decoration: none;
}

.wc-block-grid__product .wc-block-grid__product-onsale {
  border: none;
  z-index: 10;
}

.wc-block-grid__product .wc-block-grid__product-rating {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 0 20px 0;
}

.wc-block-grid__product-rating .star-rating:before,
.wc-block-grid__product-rating .wc-block-grid__product-rating__stars:before {
  opacity: 1;
}

.wc-block-grid__product-rating .star-rating span:before,
.wc-block-grid__product-rating .wc-block-grid__product-rating__stars span:before {
  color: var(--xe_woocommerce_star_color);
}

/* -------------------------------------------------------------------------- 
	#. WooCommerce Block: Reviews
   -------------------------------------------------------------------------- */
.wc-block-components-review-list .wc-block-components-review-list-item__item {
  margin-bottom: 30px;
  padding-bottom: 10px;
  border-bottom: 1px solid var(--xe_border_color);
}

.wc-block-components-review-list .wc-block-components-review-list-item__item:last-child {
  padding-bottom: 0;
  border-bottom: 0;
}

.wc-block-components-review-list .wc-block-review-list-item__product {
  font-size: var(--xe_base_font_size);
  font-weight: var(--xe_base_font_weight);
  line-height: var(--xe_base_font_line_height);
  margin-right: 10px;
}

.wc-block-components-review-list .wc-block-review-list-item__product > a {
  color: var(--xe_base_text_color);
}

.wc-block-components-review-list .wc-block-review-list-item__product > a:hover {
  color: var(--xe_seconary_color);
}

.wc-block-components-review-list-item__meta {
  font-size: var(--xe_base_font_size);
  font-size: calc(var(--xe_base_font_size) - 2px);
}

/* -------------------------------------------------------------------------- 
	#. WooCommerce Block: Price Filter
   -------------------------------------------------------------------------- */
.wp-block-woocommerce-price-filter > h3 {
  font-size: var(--xe_base_font_size);
  font-weight: var(--xe_base_font_weight);
  margin: 20px 0 30px 0;
}

.wc-block-components-price-slider__range-input-wrapper {
  margin: 0 0 40px 0;
}

.wc-block-components-price-slider__range-input-wrapper,
.wc-block-components-price-slider__range-input-progress {
  height: 4px;
}

.wc-block-components-price-slider__range-input {
  top: -4px;
}

/* -------------------------------------------------------------------------- 
	#. WooCommerce Block: Product Categories
   -------------------------------------------------------------------------- */
.wc-block-product-categories {
  margin-bottom: 30px;
}

.wc-block-product-categories-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.wc-block-product-categories-list li {
  display: block;
  margin: 10px 0;
}

.wc-block-product-categories-list li a {
  display: inline-flex;
  flex-wrap: wrap;
}

.wc-block-product-categories-list ul {
  margin: 0 0 0 20px;
}