/* -------------------------------------------------------------------------- 
	#. Addonify Quickview
   -------------------------------------------------------------------------- */

#addonify-quick-view-modal {

    height: 60vh;
    max-height: 90vh;
    min-height: 500px;
    border-radius: 5px;
    box-shadow: 0 0 65px rgba(0, 0, 0, 0.10);

    .adfy-qvm-head {

        right: 20px;
        z-index: 10000;
    }

    #addonify-qvm-close-button {

        display: inline-flex;
        justify-content: center;
        align-items: center;
        fill: var(--xe_base_text_color);
        color: var(--xe_base_text_color);
        border: 2px solid var(--xe_base_text_color);
        border-radius: 100%;

        svg {

            height: 14px;
            width: 14px;
            line-height: auto;
        }

        &:hover {

            fill: var(--xe_base_link_hover_color);
            color: var(--xe_base_link_hover_color);
            border-color: var(--xe_base_link_hover_color);
        }
    }

    .adfy-quick-view-modal-content {

        .woocommerce {

            .product {

                .summary {

                    .summary-content {

                        .product_title {

                            font-size: 18px;
                            font-weight: var(--xe_heading_font_weight);
                        }

                        .woocommerce-product-rating {

                            margin-top: 10px;
                        }

                        .price {

                            margin: 10px 0;
                        }
                    }
                }
            }
        }
    }
}

.adfy-quick-view-overlay {

    background: rgba(238, 238, 238, 0.98);
}


@media ( max-width: 768px ) {

    #addonify-quick-view-modal {

        height: 80vh;
        max-height: 80vh;
        min-height: 400px;

        .adfy-qvm-head {

            top: 5px;
            right: 5px;
        }

        .adfy-quick-view-modal-content {

            padding: 30px;
        }
    }
}